import React, { Component } from 'react';
import moment from 'moment';
import { Table, Grid, Row, Col } from 'react-bootstrap';
import { validateDuration, convertDuration, convertNumberToTime, convertTimeToNumber } from './container';
import './main.css';
import CellComponent from './cellComponent';
import { translate } from './cellComponent';

class TimeSheet extends Component {

  isToday(date) {
    console.log(moment(date, 'YYYY-MM-DD').isSame(moment().format('YYYY-MM-DD')))
    return moment(date, 'YYYY-MM-DD').isSame(moment().format('YYYY-MM-DD'));
  }

  makeProjectTitle(tasks) {
    const task = {};
    Object.keys(tasks).map((tsk) => {
      const temp = tasks[tsk];
      let title = `${temp.project}`;
      if (temp.taskId) {
        title =  `${title} / ${temp.task}`;
      }
      task[tsk] = title;
    });
    return task;
  }
  
  render() {
    const backgroundColor = '#DDDDDD';
    const { isLarge } = this.props;
    let divStyle = {overflowX: 'auto',overflowY: 'hidden', display: 'inline-block', backgroundColor: '#FFFFFF', borderBottom: '1px solid #DDDDDD'};
    if (isLarge) {
      divStyle = {
        ...divStyle,
        // flex: 1,
      }
    }
    return (
      <div className="timesheet-content">
        <div className="task-content" > 
          <CellComponent 
            bodyStyleClass="text-content"
            footer={
              <button 
                className="addLine"
                onClick={() => this.props.addLine()}
              >
                ADD A LINE
              </button>
            }
            data={this.makeProjectTitle(this.props.tasks)} 
            isField={false}
          />
        </div>
        <div style={{ ...divStyle, maxWidth: '65%' }}>
          <div className="duration-content" style={{ width: (Object.keys(this.props.dateWise).length * 120) }}>
            {
              this.props.dateWise &&
              Object.keys(this.props.dateWise).map((r, i) => (
                <div key={i} style={{ width: 120, display: 'inline-block', height: '100%' }}>
                  <CellComponent 
                    header={r} 
                    isToday={this.isToday(r)}
                    isField={true}
                    index={i+1}
                    noOfDays={this.props.rowDates.length}
                    data={this.props.dateWise[r]} changeDuration={(d) => this.props.changeDuration(d)}
                  />
                </div>
              ))
            }
          </div>
        </div>
        <div className="footer-content" style={{ maxWidth: '10%', width: '5%', flex: 1 }}>
          <CellComponent 
            bodyStyleClass="footer-context"
            cellBackGroundColor="#334250"
            header={translate('Total')} 
            footer={convertNumberToTime(`${this.props.grandTotal}`)}
            data={this.props.taskTotal} 
            isField={false}
          />
        </div>
      </div>     
    );
  }
}

export default TimeSheet;



