import React, { Component } from 'react';

const headers = {
  'Accept': 'application/json',
  'Content-Type': 'application/json',
  'X-Requested-With': 'XMLHttpRequest',
};

import request from 'superagent';

class Services {

  constructor(props) {
    this.state = {
      baseURL: '/axelor-erp/',
      restURL: '../../ws/rest/',
      actionURL: '../../ws/action/',
      // actionURL: '/axelor-erp/ws/action/',
      // restURL: '/axelor-erp/ws/rest/',
      actionName: 'com.axelor.apps.hr.service.timesheet.TimesheetService:getCurrentOrCreateTimesheet()',
    }
  }

  doLogin(userName, password) {
    const context = this;
    request
    .post(`${this.state.baseURL}login.jsp`)
    .withCredentials()
    .send({ username: userName, password: password })
    .set('Accept', 'application/json')
    .set('Content-Type', 'application/json')
    .set('X-Requested-With', 'XMLHttpRequest')
    .end(function(err, res){
      console.log(err, res);
      context.fetchTimesheet();
    });
  }

  info() {
    fetch(`${this.state.baseURL}ws/app/info`, {
      method: 'GET',
      credentials: 'include',
      headers,
    }).then((res) => console.log(res))
  }

  updateModel(model, entity) {
    return fetch(`${this.state.restURL}${entity}`, {
      method: 'POST',
      credentials: 'include',
      headers,
      body: JSON.stringify({
        "data": model
      })
    }).then((response) => {
      return response.json();
    }).then((body) => {return body})
  }

  updateTimeSheet(data, version, id) {
    const timeSheetLine = [];
    data.forEach(line => {
      const record = {};
      record['id'] = line.id || null;
      record.version = line.version;
      record.project = {id: line.projectId};
      record.product = {id: line.taskId};
      record.visibleDuration = line.duration;
      record.durationStored = line.duration;
      record.date = line.date;
      timeSheetLine.push(record);
    });
    return new Promise((resolve, reject) => {
      request
      .post(`${this.state.restURL}com.axelor.apps.hr.db.Timesheet`)
      .withCredentials()
      .send({
        data: {
          id,
          version,
          timesheetLineList: timeSheetLine,
        }
      })
      .set('Accept', 'application/json')
      .set('Content-Type', 'application/json')
      .set('X-Requested-With', 'XMLHttpRequest')
      .end(function(err, res){
        if (res) {
          resolve(res);
        } else {
          reject(err);
        }
      });
    })
  }

  fetchTimesheet(timeSheetId = null, entity) {
    const context = this;
    let id;
    return new Promise((resolve, reject) => {
      request
      //.post(`${this.state.actionURL}${this.state.actionName}`)
      .post(`${this.state.restURL}${entity}/${timeSheetId}/fetch`)
      .withCredentials()
      .send({ 
        "data":{},
      })
      .set('Accept', 'application/json')
      .set('Content-Type', 'application/json')
      .set('X-Requested-With', 'XMLHttpRequest')
      .end(function(err, res){
        const { body } = res;
        let record = body.data[0];
        id = record.id;
        const criteria = [];
        const value = []; 
        if (record.timesheetLineList.length > 0) {
          record.timesheetLineList.forEach(({ id }) => value.push(id));
        }
        const sortBy = ["id"];
        criteria.push({ fieldName: 'id', operator: 'in', value });
        context.search('com.axelor.apps.hr.db.TimesheetLine',criteria, 0, -1, sortBy, []).then((body) => {
          const timeline = [];
          body.data.length &&
          body.data.forEach(task => {
            timeline.push({
              id: task.id,
              version: task.version,
              projectId: task.project.id,
              project: task.project.fullName,
              date: task.date,
              duration: Number(task.visibleDuration),
              taskId: task.product.id,
              task: task.product.fullName,
            })
          })
          resolve({ timeline, timesheetVersion: record.version, timesheetId: id, fromDate: record.fromDate, toDate: record.toDate });
        }, reject);
      })
    })
  }

  getAction(model, action) {
    let body = JSON.stringify({
      "action": `${action}`,
      "model": `${model}`,
      "data": {},
    });
    return new Promise((resolve, reject) => {
      request
      .post(`${this.state.actionURL}`)
      .withCredentials()
      .send(body)
      .set('Accept', 'application/json')
      .set('Content-Type', 'application/json')
      .set('X-Requested-With', 'XMLHttpRequest')
      .end(function(err, res){
        console.log(err, res);
        if (res) {
          resolve(res.body.data[0]);
        } else {
          reject(err);
        }
      });
    });
  }

  search(entity, criteria = [], offset, limit, sortBy,fields = [], domain = null) {
    let body = JSON.stringify({
        "data": {
          _domain: domain,
          criteria: criteria,
          operator: 'and',
        },
        "offset": offset,
        "limit": limit,
        "sortBy": sortBy
      });
    if (fields.length > 0) {
      let temp = JSON.parse(body);
      temp.fields = fields;
      body = JSON.stringify(temp);
    }

    return fetch(`${this.state.restURL}${entity}/search`, {
      method: 'POST',
      credentials: 'include',
      headers,
      body,
    }).then((response) => {
      return response.json();
    }).then((body) => {
      return body;
    });
  }

}

export default Services;



