export const validateDuration = (duration) => {
  const numbers = duration.split(":");
  if (numbers.length <= 2 && numbers.length >=1) {
    const [hr, mins] = numbers;
    if (((!isNaN(hr) && mins === undefined && hr <= 60 && hr >=0) || (mins !== undefined && !isNaN(mins) && mins >= 0 && mins <= 60))) return true;
  }
  return false;
}

export const convertDuration = (duration) => {
  const numbers = duration.split(".");
  const [hr, mins] = numbers;
  let newDuration = null;
  let newHr = hr === undefined ? 0 : hr;
  let newMins = mins === undefined ? 0 : mins;
  if (mins > 59) {
    newMins = mins % 60;
    newHr += mins / 60;
  }
  newDuration = `${newHr}:${newMins}`;
  return newDuration;
}

export const convertTimeToNumber = (duration) => {
  const time = duration.split(":");
  const [hr, mins] = time;
  let newDuration = null;
  let newHr = hr === undefined ? 0 : Number(hr);
  let newMins = mins === undefined ? 0 : Number(mins);
  if (mins > 59) {
    newMins = mins % 60;
    newHr += mins / 60;
  }
  newDuration = newHr + (newMins / 60);
  return `${newDuration}`;
}

const convertToTimeDigit = (number = null) => {
  let newNumber = '00';
  if (number.length >= 2) {
    newNumber = number;
  } else if (number.length === 1) {
    newNumber = `0${number}`;
  }
  return newNumber;
}

export const convertNumberToTime = (duration) => {
  let time = duration;
  if (duration.indexOf(":") < 0) {
    const numbers = duration.split(".");
    const [hr, mins] = numbers;
    time = convertToTimeDigit(hr);
    let minutes = convertToTimeDigit(`${Math.round(parseFloat(`0.${mins}`) * 60)}`);
    time = `${time}:${minutes}`;
  }
  return time;
}

