import React, { Component } from 'react';
import moment from 'moment';
import { Table, Grid, Row, Col } from 'react-bootstrap';

import { validateDuration, convertDuration, convertNumberToTime, convertTimeToNumber } from './container';

export function translate(str) {
  if (window._t && typeof str === 'string') {
    return window._t(str);
  }
  return str;
}

class CellComponent extends Component {

  constructor(props) {
    super(props);
    this.state = {
      data: {
        tasks: {},
      },
      isField: true,
    }
  }

  getData(taskIndex, duration) {
    const task = this.props.data.tasks[taskIndex];
    return { date: task.date, projectId: task.projectId,taskId: task.taskId,task: task.task, duration: parseFloat(duration === '' ? 0 : duration)};
  }

  componentDidMount() {
    if (this.props.isField) {
      let data = {};
      Object.keys(this.props.data.tasks).forEach(d => {
        let obj = this.props.data.tasks[d];
        obj.duration = convertNumberToTime(obj.duration);
        data[d] = obj;
      })
      console.log(data);
      this.setState({ data: {...this.props.data, tasks: data}, isField: this.props.isField });
    } else {
      this.setState({ data: this.props.data, isField: this.props.isField });
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.isField) {
      let data = {};
      Object.keys(nextProps.data.tasks).forEach(d => {
        let obj = nextProps.data.tasks[d];
        obj.duration = convertNumberToTime(obj.duration);
        data[d] = obj;
      })
      console.log(data);
      this.setState({ data: {...nextProps.data, tasks: data}, isField: nextProps.isField });
    } else {
      this.setState({ data: nextProps.data, isField: nextProps.isField });
    }
  } 

  render() {
    const { isField, isToday, cellBackGroundColor } = this.props;
    let backgroundColor = this.props.cellBackgroundColor || '#FFFFFF';
    backgroundColor = this.props.isToday ? 'aliceblue' : backgroundColor;
    // console.log(this.state.isField, this.state.data);
    const styles = {
      backgroundColor: cellBackGroundColor !== undefined && `${cellBackGroundColor}`,
      color: cellBackGroundColor && 'white',
      borderTop: cellBackGroundColor && '0px !important',
    }
    return (
      <div style={{ position: 'relative', minWidth: this.state.isField ? 120 : 'auto',height: '100%',borderTop: '1px solid #DDDDDD' }}>
        
        <div style={{ backgroundColor, }}>
          {
            isField ?

            <div className="cell-height cell-header" style={{ height: 50 }}>
              <div style={{ height: 'inherit', padding: 10, paddingBottom: 20, backgroundColor: isToday && 'rgba(2,117,216, 0.298039)' }}>
                <span style={{ display: 'block', fontWeight: 'bold'}}>{translate(moment(this.props.header, 'YYYY-MM-DD').format('dddd'))}</span>
                <span style={{ display: 'block', fontWeight: 'bold'}}>
                  {translate(moment(this.props.header, 'YYYY-MM-DD').format('MMM'))}{` `}
                  {translate(moment(this.props.header, 'YYYY-MM-DD').format('DD'))}
                </span>
              </div>
            </div>
            :
            <div 
              className="cell-height cell-header" 
              style={{ ...styles, padding: 10, paddingBottom: 20 , height: 50, fontWeight: 'bold' }}>
              <span>{translate(this.props.header)}</span>
            </div>
          }
          <div className={this.props.bodyStyleClass}>
            {this.props.isField ?
              Object.keys(this.state.data.tasks).map((task, index) => (
                <div className="cell-height" key={index} style={{ borderTop: '1px solid #DDDDDD', backgroundColor: index % 2 !== 0 ? '#FFFFFF' : '#F2F2F2' }}>
                  <div style={{ height: 'inherit', backgroundColor: isToday && 'rgba(2,117,216, 0.3)',}}>
                    {
                      task.startsWith('dummy') ? 
                        <span></span>
                      :
                        <input 
                          className="duration-input" 
                          type="text" 
                          tabIndex={(index * this.props.noOfDays) + this.props.index}
                          style={{ backgroundColor: 'transparent'}}   //index % 2 !== 0 ? '#FFFFFF' : '#F2F2F2'
                          value={this.state.data.tasks[task].duration} 
                          onChange={(e) => { 
                              if (validateDuration(e.target.value)) {
                                let data = this.state.data;
                                data.tasks[task].duration = e.target.value;
                                this.setState({
                                  data,
                                })
                              }
                            }
                          }
                          onBlur={(e) => {
                            if (validateDuration(e.target.value)) {

                              console.log(e.target.value, convertTimeToNumber(e.target.value))
                              this.props.changeDuration(this.getData(task, convertTimeToNumber(e.target.value)));
                            }
                          }}
                        />
                    }
                  </div>
                </div>
              ))
            :
              Object.keys(this.props.data).map((task, index) => (
                <div 
                  className="cell-height" 
                  key={index} 
                  style={{ 
                    ...styles, 
                    //minWidth: 75,
                    borderTop: !cellBackGroundColor && '1px solid #DDDDDD', 
                    overflowY: 'auto', 
                    backgroundColor: cellBackGroundColor ? styles.backgroundColor : index % 2 !== 0 ? '#FFFFFF' : '#F2F2F2', 
                  }}
                >
                  <div style={{ padding: '2px 5px'}}>{isNaN(this.props.data[task]) || this.props.data[task] === '' ? this.props.data[task] : convertNumberToTime(this.props.data[task])}</div>
                </div>
              ))
            }
          </div>
          <div className={`task-footer`} style={{ color: 'white', borderTop: !cellBackGroundColor && '1px solid #DDDDDD', backgroundColor: '#334250', textAlign: 'center' }}>
            {
              isToday ?
              <div style={{ height: 'inherit', backgroundColor: 'rgba(2,117,216, 0.3)' }}>
                <span style={{ display: 'block', fontWeight: 'bold', paddingTop: 10}}>{translate(this.props.footer) || convertNumberToTime(this.props.data.total) }</span>  
              </div>
              :
              <span style={{ display: 'block', fontWeight: 'bold', paddingTop: 10}}>{translate(this.props.footer) || convertNumberToTime(this.props.data.total) }</span>
            }
          </div>
        </div>
      </div>
    );
  }
}

export default CellComponent;



